      width: 8.75rem;
      height: 3.125rem; } }

.toggle-switch__checkbox {
  display: none; }

.toggle-switch__slider {
  position: absolute;
  cursor: pointer;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background-color: #FFF;
  transition: .4s;
  border-radius: 0.8125rem;
  border: 0.0625rem solid rgba(154, 154, 154, 0.4); }
  .toggle-switch__slider::before {
    position: absolute;
    content: "";
    height: 1.125rem;
    width: 1.125rem;
    left: 0.25rem;
    bottom: 0.15625rem;
    background-color: #308DE8;
    transition: .4s;
    border-radius: 50%; }
    @media (max-width: 60em) {
      .toggle-switch__slider::before {
        width: 2.5rem;
        height: 2.5rem; } }

.toggle-switch__checkbox:checked + .toggle-switch__slider::before {
  -webkit-transform: translateX(2.0625rem);
          transform: translateX(2.0625rem); }
  @media (max-width: 60em) {
    .toggle-switch__checkbox:checked + .toggle-switch__slider::before {
      -webkit-transform: translateX(5.3125rem);
              transform: translateX(5.3125rem); } }

